.slide {
    display: flex;
    flex-basis: 100%;
    justify-content: center;
    max-width: 100%;
    overflow: hidden;
    position: relative;

    &::before {
        background-image: url('../files/squiz-brushmarks.svg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        content: '';
        display: block;
        height: 100%;
        opacity: .1;
        position: absolute;
        width: 100%;
    }
}

.slide-style-1 {
    background-color: #343434;
    color: #ffffff;
}

.slide-style-2 {
    background-color: #3297b6;
    color: #ffffff;
}

.slide-style-3 {
    background-color: #e05541;
    color: #ffffff;

    &::before {
        transform: rotate(0.5turn)
    }
}

.slide-style-4 {
    background-color: #e4e4e3;
    color: #343434;

    &::before {
        opacity: .4;
    }
}

.slide-wrapper {
    align-content: center;
    display: flex;
    flex-basis: 80%;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 80%;
    padding: 50px;
    position: relative;
}

.slide-sq-logo {
    background-color: #343434;
    height: 60px;
    padding: 15px 20px;
    position: absolute;
    right: 50px;
    top: 0;
    width: 132px;

    &::before {
        background-image: url('../files/squiz-logo.png');
        background-repeat: no-repeat;
        background-size: contain;
        content: '';
        display: block;
        height: 60px;
        width: 132px;
    }
}

.slide-title {
    flex-basis: 100%;
    font-size: 50px;
    margin: 20px 0;
    max-width: 100%;
    text-align: center;
}

.slide-image {
    flex-basis: 100%;

    img {
        max-height: 400px;
        margin: 0 auto;
        display: block;
    }
}

.slide-content {
    font-size: 22px;
    font-weight: 600;
    justify-content: center;
    line-height: 1.8;
    margin: 20px 0;

    >* {
        font-size: 22px;
        font-weight: 600;
        line-height: 1.8;
    }
}

.slide-code {
    margin: 20px 0;
    overflow: hidden;
    padding-top: 40%;
    position: relative;
    width: 100%;

    &.hide {
        padding: 0;
    }

    iframe {
        border: 0;
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;

        &.hide {
            opacity: 0;
        }
    }
}

pre {
    background-color: #1d1f21;
    border-radius: 2px;
    box-shadow: 0 20px 25px -20px rgba(0, 0, 0, 0.7);
    color: #c5c8c6;
    margin: 30px 0 40px 0;
    overflow: auto;
    position: relative;
}

code {
    background-color: #1d1f21;
    color: #c5c8c6;
    display: block;
    line-height: 1.6;
    overflow-x: auto;
    padding: 20px;
    white-space: pre;
}

.slide-loader {
    display: block;
    height: 100px;
    position: relative;
    width: 100%;
}

.slide-loading {
    position: absolute;
    top: calc(50% - 20px);
    left: calc(50% - 20px);
}

.slide-box {
    width: 50px;
    height: 50px;
    background: #343434;
    animation: animate .5s linear infinite;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 3px;
}

.slide-shadow {
    width: 50px;
    height: 5px;
    background: #000;
    opacity: 0.1;
    position: absolute;
    top: 59px;
    left: 0;
    border-radius: 50%;
    animation: shadow .5s linear infinite;
}


@keyframes loader {
    0% {
        left: -100px
    }

    100% {
        left: 110%;
    }
}

@keyframes animate {
    17% {
        border-bottom-right-radius: 3px;
    }

    25% {
        transform: translateY(9px) rotate(22.5deg);
    }

    50% {
        transform: translateY(18px) scale(1, .9) rotate(45deg);
        border-bottom-right-radius: 40px;
    }

    75% {
        transform: translateY(9px) rotate(67.5deg);
    }

    100% {
        transform: translateY(0) rotate(90deg);
    }
}

@keyframes shadow {
    50% {
        transform: scale(1.2, 1);
    }
}